body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}
body a {
  cursor: pointer;
}
body button {
  cursor: pointer;
}
body select {
  cursor: pointer;
}
body input {
  font-size: 16px;
}
body select {
  font-size: 16px;
}
.linkListDisplayHeader {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background-color: whitesmoke;
}
a:link {
  color: black;
  font-weight: 700;
  text-decoration: none;
  -webkit-user-select: none;
  /* disable selection/Copy of UIWebView */
  -webkit-touch-callout: none;
  /* disable the IOS popup when long-press on a link */
}
a:hover {
  color: black;
  text-decoration: underline;
}
a:active {
  color: black;
  background-color: grey;
  text-decoration: underline;
}
a:visited {
  color: lightslategray;
  text-decoration: underline;
}
.homepageImageHolder {
  flex-direction: row;
  width: 100%;
  flex-wrap: nowrap;
}
.homepageImagePhoto {
  width: 100%;
