}
.App {
  min-height: 100vh;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}
.linkList {
  display: grid;
  height: 100vh;
  width: 100%;
  grid-template-rows: auto 1fr auto;
}
header {
  overflow: hidden;
  font-family: 'Mulish', 'Oswald', 'ZCOOL KuaiLe', 'Namun Gothic', 'Yusei Magic', 'Almarai', 'Assistant', 'Source Sans Pro', 'Kanit', 'Inter', sans-serif;
