main {
  margin-top: 25vh;
  width: 100%;
  max-width: 800px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  margin: 0 auto;
  padding-top: 1.75vh;
  font-family: 'Mulish', 'Oswald', 'ZCOOL KuaiLe', 'Namun Gothic', 'Yusei Magic', 'Almarai', 'Assistant', 'Source Sans Pro', 'Kanit', 'Inter', sans-serif;
  font-weight: 400;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-auto-rows: min-content;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(70deg, white, darkGray);
  background-size: contain;
  background-position: top;
  background-attachment: fixed;
}
main a {
  font-family: 'Mulish', 'Oswald', 'ZCOOL KuaiLe', 'Namun Gothic', 'Yusei Magic', 'Almarai', 'Assistant', 'Source Sans Pro', 'Kanit', 'Inter', sans-serif;
  font-weight: 700;
  display: block;
}
main p {
  display: block;
  line-height: 1.1;
  white-space: pre-wrap;
}
main img {
  padding: 1.5%;
  max-height: 165px;
  max-width: 165px;
}
.addLinkPreviewImage {
  max-height: 45px;
  max-width: 165px;
}
main div {
  border: 2px solid black;
  width: 80%;
  margin: 1.5vh auto;
  border-radius: 1vh;
  flex: 1 1 450px;
  display: flex;
  flex-direction: column;
  padding: 1.5vh;
}
main div .linkSquare {
  min-height: 205px;
  height: 100%;
  background-color: white;
}
main div .linkDescription {
  overflow: hidden;
}
main div .linkDescription {
  text-align: left;
  padding: 0 5px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  word-wrap: break-word;
}
main div .linkDescriptionTag {
  padding-top: 0.8vh;
  padding-bottom: 0.8vh;
  cursor: pointer;
}
.editLinkDescription {
  width: 60%;
  min-height: 20vh;
  text-align: center;
}
.linkSquare {
  background-color: whitesmoke;
}
.linkSquare a:link {
  border-bottom: 1px solid green;
}
.linkSquare a:visited {
  border-bottom: 1px solid red;
}
.linkSquare .linkSquareButtonHolder {
  display: flex;
  flex-direction: row;
  max-height: 2.25em;
  justify-content: space-evenly;
  align-items: center;
}
.linkSquare .linkSquareButtonHolder a:link {
  border-bottom: none;
}
.linkSquare .linkSquareButtonHolder a:hover {
  background-color: darkgray;
}
.linkSquare .linkSquareButtonHolder a:visited {
  border-bottom: none;
}
.linkSquare .sqaureButton {
  height: 100%;
}
.linkSquare .sqaureButton:hover {
  background-color: darkgray;
}
.linkSquare:last-child {
  margin-bottom: 10vh;
}
footer {
  overflow: hidden;
  font-family: 'Mulish', 'Oswald', 'ZCOOL KuaiLe', 'Namun Gothic', 'Yusei Magic', 'Almarai', 'Assistant', 'Source Sans Pro', 'Kanit', 'Inter', sans-serif;
  font-weight: 900;
  border-top: 0.25vh solid black;
}
footer span {
  cursor: pointer;
}
.footerLink {
  font-weight: 900;
  color: white;
}
.active {
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
}
.dashboard2 header {
  max-height: 70vh;
}
.dashboard2 tspan {
  color: #252525;
  fill: #252525;
}
.dashboard2 hr {
  background-color: black;
  border-color: black;
  width: 100%;
}
.colorPickerHolder {
  margin: 5px auto;
  width: 220px;
}
.linkListDashboardHeader {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.linkListDashboardHeader header {
  max-height: 35vh;
}
.linkListDashboardHeader .dashboardToggleHolder {
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  overflow: hidden;
}
.linkListDashboardHeader .dashboardToggleHolder p {
  border: 1px solid black;
  padding: 1em;
  margin: 1em;
  cursor: pointer;
}
.linkListDashboardHeader .dashboardToggleHolder p:hover {
  background-color: darkgray;
  cursor: pointer;
}
.settingsPanel span {
  cursor: pointer;
}
.settingsPanel button {
  cursor: pointer;
}
.settingsPanel a {
  cursor: pointer;
}
.dashboardInfoSection {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2em;
  text-align: center;
  word-break: break-all;
}
.dashboardInfoSection span {
  cursor: pointer;
}
.dashboardInfoSection h2 {
  font-weight: bolder;
}
.dashboardInfoSection table {
  margin: 0 auto;
  border: 1px solid black;
  padding: 1em;
  border-collapse: collapse;
  width: 80%;
}
.dashboardInfoSection table td {
  border: 1px solid black;
  padding: 1em;
  word-break: break-word;
}
.darkMode {
  background-color: black;
}
.darkMode a:link {
  color: white;
}
.darkMode a:hover {
  color: white;
}
.darkMode a:active {
  color: white;
}
.darkMode a:visited {
  color: lightslategray;
}
.darkMode header {
  border-bottom: 0.25vh solid grey;
  background-color: black;
  color: white;
}
.darkMode header img {
  border-radius: 1vh;
  padding: 0.25vh;
  border: 0.25vh solid grey;
}
.darkMode main {
  background-image: linear-gradient(70deg, #151515, black);
  background-size: contain;
  background-position: top;
}
.darkMode main div {
  border: 2px solid grey;
  background-color: black;
  color: white;
}
.darkMode main div p {
  color: grey;
}
.darkMode main div img {
  background-color: dimgrey;
  border-radius: 1vh;
}
.darkMode .linkList {
  background-color: black;
}
.darkMode .drawer {
  color: white;
}
.darkMode .dashboard2 hr {
  background-color: white;
  border-color: white;
}
.darkMode .dashboard2 tspan {
  fill: #ffffff;
  color: #ffffff;
}
.darkMode .linkListDashboardHeader .dashboardToggleHolder {
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  overflow: hidden;
}
.darkMode .linkListDashboardHeader .dashboardToggleHolder p {
  border: 1px solid white;
  cursor: pointer;
}
.darkMode .linkListDashboardHeader .dashboardToggleHolder p:hover {
  background-color: darkgray;
  cursor: pointer;
}
.darkMode .dashboardInfoSection {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 2em;
  text-align: center;
}
.darkMode .dashboardInfoSection table {
  margin: 0 auto;
  border: 1px solid white;
  padding: 1em;
  border-collapse: collapse;
  width: 80%;
}
.darkMode .dashboardInfoSection table td {
  border: 1px solid white;
  padding: 1em;
}
